.pagination {
   	display: flex;
	justify-content: center;
	margin: 20px auto;

	.pagination-list {
		display: flex;
		justify-content: space-between;
		width: 180px;
		padding: 0;
	}

    .pagination-item {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		height: 30px;
		font-weight: normal;
		text-align: center;
		line-height: 20px;
		font-size: 16px;
		color: $light-color;
		background: transparent;
		border: none;
		margin-right: 15px;
		&:hover {
            cursor: pointer;
		}
        &active {
            border: 1px solid $accent-color;
            border-radius: 10px 0px;
            color: $accent-color;
          }
	}
}